.app {
  text-align: center;
  font-family: Arial, sans-serif;
  margin-top: 50px;
}

h1 {
  color: #333;
}

button {
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
}

button:hover {
  background-color: #0056b3;
}
